html{
  background: -webkit-linear-gradient(left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
  background: linear-gradient(to right, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%); /*w3c*/
  background-size: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

