   box-shadow: 0 0 2rem .8rem rgba( 255, 255, 255, 0.8 )
}

.backGradient{
 background: -webkit-radial-gradient(center, ellipse, rgb(23, 54, 141) 0%, rgba(0,128,128,1) 100%);
